<template>
  <div>
    <div class="modal add-product-modal" ref="modal">
      <div class="modal-dialog modal-lg">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
            <h3 class="modal-title">Danh sch sn phm</h3>
          </div>
          <div class="modal-body" v-if="show">
            <div class="row">
              <div class="col-md-6">
                <div class="input-group">
                  <input type="text" placeholder="Nhp tn sn phm..." @keyup.13="search" v-model="keyword" class="form-control">
                  <span class="input-group-btn">
                    <button v-if="keyword.length>0" @click="()=>{keyword=''; search()}" class="btn btn-defal btn-flat">X</button>
                    <button @click="search" type="button" class="btn btn-primary btn-flat">Tm kim</button>
                  </span>
                </div>
              </div>
              <div class="col-md-6">
                <button type="button" class="btn btn-danger btn-flat btn-sm pull-right" data-dismiss="modal">Hy <i class="fa fa-ban" aria-hidden="true"></i></button>
                <button type="button" @click="save_close()" class="btn btn-flat btn-primary btn-sm pull-right">a sn phm  chn gi hng <i class="fa fa-floppy-o" aria-hidden="true"></i></button>
              </div>
            </div>
            <div class="clearfix"></div>
            <br/>
            <div class="wrap-table">
              <table class="table-bordered table" style="width:1500px">
                <thead>
                  <tr>
                    <th>.No</th>
                    <th>Chn</th>
                    <th>M sn phm</th>
                    <th style="width:200px;">Tn sn phm</th>
                    <th>Tn n v tnh</th>
                    <th>S lng n v/hp</th>
                    <th>Gi hp</th>
                    <th>Gi n v</th>
                    <th>SL c bn (hp)</th>
                    <th>SL c bn (n v)</th>
                    <th>S hp ng thu</th>
                    <th v-if="$store.state.user_info.clnType=='ETC'">Sl tn thu (hp)</th>
                    <th v-if="$store.state.user_info.clnType=='ETC'">Sl tn thu (n v)</th>
                    <th>Sl d tr (hp)</th>
                    <th>Sl d tr (n v)</th>
                    <th v-if="$store.state.user_info.clnType=='ETC'">M l sn phm</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(product,index) in products" @click="check($event)" v-bind:key="index">
                    <td>{{(index+1)}}</td>
                    <td>
                      <template>
                      </template>
                      <input v-model="selectedsProductID" v-bind:id="'checkbox-product-'+index" v-bind:value="index" type="checkbox" name="selectedsProductID[]" value="" />
                    </td>
                    <td>{{product.itemID}}</td>
                    <td class="td-checkbox"><label v-bind:for="'checkbox-product-'+index" class="text-info">{{product.itemName}}</label></td>
                    <td>{{product.itemUnitName}}</td>
                    <td>{{product.itemPerBox}}</td>
                    <td>{{product.itemPrice | formatVnd}}</td>
                    <td>{{product.storePrice | formatVnd}}</td>
                    <td>{{product.slOhItQt}}</td> <!-- S lng c bn hp-->
                    <td>{{product.slOhQtty}}</td> <!-- S lng c bn n v-->
                    <td>{{product.qc_XaBang}}</td> <!--S hp ng thu -->
                    <td v-if="$store.state.user_info.clnType=='ETC'">{{product.rmRfItQt}}</td> <!-- S lng tn thu hp-->
                    <td v-if="$store.state.user_info.clnType=='ETC'">{{product.rmRfQtty}}</td> <!-- S lng tn thu n v-->
                    <td>
                      {{product.rmPlItQt}}
                    </td> <!-- S lng d tr hp-->
                    <td>{{product.rmPlQtty}}</td> <!-- S lng d tr n v-->
                    <td v-if="$store.state.user_info.clnType=='ETC'">{{product.bchCode}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="text-right hidden">
              <ul class="pagination pagination-sm">
                <template v-if="pagination.currentPage>1">
                  <li>
                    <a class="page-link" href="#" tabindex="-1" @click="loadPage(pagination.currentPage - 1)"><i class="fa fa-arrow-left"></i></a>
                  </li>
                  <li v-if="pagination.currentPage > pagination.max + 1">
                    <a href="#" @click="loadPage(1)">1</a>
                  </li>
                </template>
                <template v-if="start>=pagination.max">
                  <li v-if="start==pagination.max">
                    <a href="#" @click="loadPage(2)">2</a>
                  </li>
                  <li v-else>
                    <a href="#">...</a>
                  </li>
                </template>
                <template v-if="n >= start" v-for="n in end">
                  <template v-if="n==pagination.currentPage">
                    <li class="active">
                      <a class="page-link" href="#">
                        {{
n
                        }}
                      </a>
                    </li>
                  </template>
                  <template v-else>
                    <li>
                      <a class="page-link" href="#" @click="loadPage(n)">{{n}}</a>
                    </li>
                  </template>
                </template>

                <template v-if="end + 1 < totalPages">
                  <li v-if="end + 2 == totalPages">
                    <a href="#" @click="loadPage(totalPages-1)">{{(totalPages-1)}}</a>
                  </li>
                  <li v-else>
                    <a href="#">...</a>
                  </li>
                </template>

                <template v-if="pagination.currentPage< totalPages">
                  <li v-if="pagination.currentPage < totalPages - pagination.max">
                    <a href="#" @click="loadPage(totalPages)">{{totalPages}}</a>
                  </li>

                  <li v-if="pagination.currentPage<totalPages">
                    <a class="page-link" href="#" @click="loadPage(pagination.currentPage + 1)"><i class="fa fa-arrow-right"></i></a>
                  </li>
                </template>
              </ul>
            </div>
          </div>
        </div>
        <!-- /.modal-content -->
      </div>
      <!-- /.modal-dialog -->
    </div>
  </div>
</template>
<script>
  export default {
    props: ["show", "membType", "customerID"],
    data() {
      return {
        // lu danh sch product  chn.
        selectedsProductID: [],
        // danh sch sn phm t api.
        products: [],
        keyword: '',
        // phn trang.
        pagination: {
          currentPage: 1,
          total: 0,
          pageSize: 20,
          max: 3
        }
      }
    },
    methods: {
      async get_products() {
        this.selectedsProductID = []; // reset lai list da chon.
        let url = "/api/product?membType=" + this.membType + "&customerID=" + this.customerID;
        if (this.keyword) {
          url += "&term=" + this.keyword;
        }
        let response = await this.$http.get(url);
        this.products = response.data;
        //this.pagination.total = response.data.total;
      },
      async loadPage(page) {
        this.$store.state.show_loading = true;
        this.pagination.currentPage = page;
        await this.get_products();
        this.$store.state.show_loading = false;
      },
      async check(evt) {

      },
      async search() {
        this.$store.state.show_loading = true;
        await this.get_products();
        this.$store.state.show_loading = false;
      },
      save_close() {
        if (this.selectedsProductID.length == 0) {
          alert("Vui lng chn sn phm  a vo gi hng !");
          return;
        }

        var details = [];
        for (let i = 0; i < this.selectedsProductID.length; i++) {
          var product = this.products[this.selectedsProductID[i]];
          var detail = {};

          detail.storeID = product.storeID;
          detail.itemID = product.itemID;
          detail.itemName = product.itemName;
          detail.itemUnit = product.itemUnit;
          detail.itemUnitName = product.itemUnitName;
          detail.qc_XaBang = product.qc_XaBang;
          detail.bchCode = product.bchCode; // m l

          // Qui cch bn
          if (this.$store.state.user_info.clnType == 'OTC') {
            detail.boxID = 'SizeItem';
          } else if (this.$store.state.user_info.clnType == 'ETC') {
            detail.boxID = 'SizeBase';
          }

          // webContractDetail khng c thng tin ny, nhng gn vo  lm d liu tnh ton.
          // Lu form type 2 khng c gi tr, call api  ly li.
          detail.itemPerBox = product.itemPerBox; //  Item per box(s lng n v/hp)
          detail.slOhQtty = product.slOhQtty; // s lng tn bn n v.
          detail.rmRfQtty = product.rmRfQtty; // s lng tn thu n v..

          // tinh so luong ton thau theo n v. (store)
          if (this.$store.state.user_info.clnType == 'OTC') { // -9999
            detail.remnRfQt = -9999;
          } else if (this.$store.state.user_info.clnType == 'ETC') { //
            detail.remnRfQt = product.rmRfQtty;
          }

          detail.itemQtty = 0;
          detail.storeQtty = 0;

          // ItemPrice gi bn
          if (this.$store.state.user_info.clnType == 'OTC') {
            detail.itemPrice = product.itemPrice; // OTC th ly gi item price
          } else if (this.$store.state.user_info.clnType == 'ETC') {
            detail.itemPrice = product.storePrice; // ETC th ly gi store price
          }

          // Thnh tin.
          detail.prdcAmnt = 0;

          // gn null
          detail.prmtID = "";
          detail.prmtListItem = "";
          detail.dscnAmnt = 0;
          detail.dscnRate = 0;

          detail.dscnMbRt = 0;
          detail.dscnMbAm = 0;

          details.push(detail);
        }

        // gi d liu qua Form, qua props selected. vi tham s l hm webContractDetail_Selected trong mixins web_contractdetail.
        this.$emit("selected", details);
      }
    },
    filters: {
      formatVnd(number) {
        var decimals = 0;
        var dec_point = ",";
        var thousands_sep = ".";
        var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
        var d = dec_point == undefined ? "," : dec_point;
        var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
        var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;

        return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "") + " ";
      }
    },
    watch: {
      show() {
        if (this.show) {
          // khi m popup, reset li list danh sch product ang chn. v t khoa tm kim.
          this.selectedsProductID = [];
          this.keyword = '';

          this.$store.state.show_loading = true;

          // Kim tra cha ly sn phm th ly ri show, cn ly ri th ch show.
          if (this.products.length == 0 || true) {

            // Goi await trong ham khong async.
            (async () => {
              await this.get_products();
              this.$store.state.show_loading = false;
              $(this.$refs.modal).modal('show');
            })();


          } else {
            this.$store.state.show_loading = false;
            $(this.$refs.modal).modal('show');
          }
        } else {
          $(this.$refs.modal).modal('hide');
        }
      }
    },
    mounted() {
      var vm = this;
      // Cu hnh khi c modal, gi hm hide  Form lng nghe.
      $(this.$refs.modal).on('hidden.bs.modal', () => {
        vm.$emit("hide");
      });
    },
    computed: {
      totalPages: function () {
        return Math.ceil(this.pagination.total / this.pagination.pageSize)
      },
      start() {
        var start = this.pagination.currentPage - this.pagination.max;
        if (start <= 1) {
          return 1;
        } else {
          return start;
        }
      },
      end() {
        var end = this.pagination.currentPage + this.pagination.max;
        if (end > this.totalPages) {
          return this.totalPages;
        } else {
          return end;
        }
      }
    },
    beforeDestroy() {
      $(this.$refs.modal).modal('hide');
    }
  }
</script>
<style lang="scss">
  input[type="checkbox"] {
    height: 15px;
    width: 15px;
    cursor: pointer;
  }

  table td {
    white-space: nowrap;
  }

  table .td-checkbox label {
    cursor: pointer;
  }

  .wrap-table {
    min-height: 450px;
  }

    .wrap-table tr:hover {
      background: #eee;
    }

  .add-product-modal .modal-body {
    min-height: 300px;
  }
</style>



